.background {
	position: absolute;
	overflow-x: hidden;
	width:  100%;
	top: -400px;
	filter: blur(1px);
  	-webkit-filter: blur(1px);
}
.container {
	position: absolute;
   	left: 50%;
   	top: 25%;
   	transform: translate(-50%, -50%);
    z-index: 3;
    color: white;
}
.container h1 {
	font-size: 60px;
    font-weight: bold;
    text-align: center;
}
.container p {
	font-size: 30px;
	text-align: center;
}
.siteBody {
	position: relative;
	height: 5000px;
	background-color: white;
	margin-top:1000px;
}